import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import accountSelect from '@/components/common-components/accountSelect';
let payAdmin = class payAdmin extends Vue {
    constructor() {
        super(...arguments);
        this.openid = '';
        this.page = 1;
        this.orderList = [];
        this.isShowMore = false;
    }
    // 计算属性
    get formatMonth() {
        return this.format(this.selectDate, 'yyyy-MM');
    }
    activated() {
        this.orderList = [{ "fee": 0.28, "body": "集合订单", "out_trade_no": "20201003165943225", "userId": "oJ-T_0irr1zac2cWKuSmRDp-xWWM", "commodityList": [{ "id": "bc8f37d0-fb41-11ea-8a76-532bd2785fca", "name": "巨峰葡萄", "num": 1, "price": "0.1" }, { "id": "cd394000-fb53-11ea-90c2-8585cc3e4cd4", "name": "山东秋月梨 秋月冰糖梨 礼盒装脆甜梨子莱阳梨 羊脂秋月梨梨5", "num": 1, "price": "0.02" }, { "id": "14067010-0244-11eb-a74a-f3981c383190", "name": "哈密瓜", "num": 1, "price": "0.01" }, { "id": "59fb86a0-0244-11eb-a74a-f3981c383190", "name": "橙子", "num": 1, "price": "0.02" }, { "id": "0889c5c0-fb00-11ea-a4a8-297c219632b8", "name": "小油菜", "num": 1, "price": "0.01" }, { "id": "eda03540-fb4b-11ea-a0c9-89bd040e4daa", "name": "鸡蛋", "num": 1, "price": "0.1" }, { "id": "0492dc50-0243-11eb-a74a-f3981c383190", "name": "新西兰原装进口高钙儿童牛奶 安佳（Anchor）金装高钙儿童牛奶 190ml*27 整箱装", "num": 1, "price": "0.01" }, { "id": "d07eba90-0244-11eb-a74a-f3981c383190", "name": "白菜", "num": 1, "price": "0.01" }], "type": "commodity", "payTime": 1601715583388 }];
        this.$store.commit('SET_SESSION_ID', 'noSession');
        this.pageInit();
    }
    pageInit() {
        let query = this.$route.query;
        if (query.openid) {
            this.openid = query.openid;
            this.getOrderList();
        }
        else if (query.code) {
            this.getUserInfo();
        }
    }
    getOrderList() {
        const toast = this['$createToast']({
            time: 0,
            txt: '数据获取中...'
        });
        toast.show();
        this.$store.dispatch('getOrderList', { openid: this.openid, page: this.page }).then((response) => {
            if (response.success) {
                let resData = response.data;
                if (response.data.list.length >= 0 && response.data.list.length < 10) {
                    this.orderList = this.orderList.concat(resData.list);
                    this.isShowMore = false;
                }
                else if (response.data.list.length === 10) {
                    this.orderList = this.orderList.concat(resData.list);
                    this.isShowMore = true;
                }
                else {
                    this.isShowMore = false;
                }
            }
            toast.hide();
        });
    }
    getUserInfo() {
        let query = this.$route.query;
        if (query.code) {
            const toast = this['$createToast']({
                time: 0,
                txt: '数据获取中...'
            });
            toast.show();
            this.$store.dispatch('getCommodityUserInfo', { code: query.code }).then((response) => {
                if (response.success) {
                    if (response.data.openid) {
                        this.openid = response.data.openid;
                        this.getOrderList();
                        if (response.data.userInfo) {
                            this.userInfo = response.data.userInfo;
                        }
                        // this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
                    }
                }
                toast.hide();
            });
        }
        else {
            console.log('不是微信登陆');
        }
    }
    showMoreClick() {
        this.page++;
        this.getOrderList();
    }
    format(date, format) {
        //eg:format="yyyy-MM-dd hh:mm:ss";
        if (!format) {
            format = 'yyyy-MM-dd hh:mm:ss';
        }
        var o = {
            'M+': date.getMonth() + 1,
            'd+': date.getDate(),
            'H+': date.getHours(),
            'h+': date.getHours(),
            'm+': date.getMinutes(),
            's+': date.getSeconds(),
            'q+': Math.floor((date.getMonth() + 3) / 3),
            'S': date.getMilliseconds()
        };
        if (/(y+)/.test(format)) {
            format = format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
        }
        for (var k in o) {
            if (new RegExp('(' + k + ')').test(format)) {
                format = format.replace(RegExp.$1, RegExp.$1.length === 1
                    ? o[k]
                    : ('00' + o[k]).substr(('' + o[k]).length));
            }
        }
        return format;
    }
};
payAdmin = __decorate([
    Component({
        components: {
            accountSelect
        },
        filters: {
            getFormatDate(date) {
                if (date) {
                    return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '号';
                }
                else {
                    return '';
                }
            },
            getImage(item) {
                if (item.commodity_info[0] && item.commodity_info[0].imgUrlList) {
                    return item.commodity_info[0].imgUrlList[0];
                }
                else {
                    return '';
                }
            },
            getType(item) {
                if (item.state === 'processed') {
                    return '配送成功';
                }
                else {
                    return '配送中';
                }
            }
        }
    })
], payAdmin);
export default payAdmin;
